@import url("../../../constants/constant.css");

.row .col .row {
    padding-left: 1.3em;

    background-color: var(--color-white);

    border-radius: 1rem;

    margin-bottom: 1rem;
}

.row .col .row button {
    width: 100%;
    height: 3em;

    font-size: 1.4em;
    font-weight: bold;
    color: var(--white);

    background-color: var(--color-primary);

    border: none;
    border-radius: var(--card-border-radius);
}

.row .col .row button:hover {
    opacity: 0.9;
}

.profile-img img {
    width: 100%;
    height: 100%;
}

.btn-upload {
    background: var(--color-primary);

    color: aliceblue;

    border: none;
}

.btn-upload:hover {
    opacity: 0.9;
}
