.row .col-img {
    width: 100%;
    height: 13em;

    padding: 0;
}

.row .col-img img {
    width: 100%;
    height: 100%;

    object-fit: cover;
}

.row .col-img span {
    cursor: pointer;

    position: relative;
}

.row .col-img span::before {
    content: "";

    width: 100%;
    height: 100%;
    
    position: absolute;
}

.row .col-img span:hover::before {
    background-color: rgba(255, 255, 255, 0.114);
}
